import styled from "styled-components"
import colors from "./colors"
import fonts from "./fonts"

export const Container = styled.div`
    width: 100%;
    height: max-content;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    font-family: ${fonts.outlined};
`

export const Header = styled.div`
    width: 100%;
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: ${colors.black};
    position: fixed;
    border-bottom: 1px;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-style: solid;
    border-color: transparent;

    & > div{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: 2px;
        border-top: 0;
        border-left: 0;
        border-right: 0;
        border-style: solid;
        border-color: ${colors.gray};
        cursor: pointer;
        font-family: ${fonts.normal};
        color: whitesmoke;

        :hover{
            border-color: ${colors.red};
        }
    }
`

export const Body = styled.div`
    width: 100%;
    height: max-content;
    color: ${colors.contrast};

    & > #about {
        width: 100%;
        height: 100vh;
        background-color: ${colors.black};
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        
        & > h1 {
            font-weight: bold;
            font-size: 81px;
            margin: 0;
        }

        & > h3 {
            font-weight: bold;
            font-size: 32px;
        }

        & > a {
            text-decoration: none;
            color: ${colors.contrast};
            font-weight: bold;
            font-family: ${fonts.normal};
            font-size: 22px;

            :hover {
                filter: opacity(75%);
            }
        }

        & > div {
            width: 150px;
            display: flex;
            justify-content: space-between;

            & > a{
                transition: 250ms;
                :hover {
                    filter: opacity(75%)
                }
            }
        }
    }    

//===================================================================================================================================================================//

    & > #education {
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: ${colors.contrast};
        color: ${colors.black};
        font-family: ${fonts.normal};

        & > h1 {
            font-family: ${fonts.outlined};
            font-size: 42px;
        }

        & > ul {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 0;
            margin: 0;
            font-size: 20px;
        }
    }

//===================================================================================================================================================================//

    & > #skills {
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: ${colors.black};
        color: ${colors.contrast};
        font-family: ${fonts.normal};

        & > div {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 70%;
            height: 100%;

            & > h1 {
                margin-top: 100px;
                font-family: ${fonts.outlined};
                font-size: 48px;
            }

            & > div {
                width: 100%;
                height: 100%;

                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr;
                justify-content: center;
                align-items: center;

                & > div {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;

                    & > h4 {
                        font-size: 18px;
                    }
                }
            }
        }
    }

//===================================================================================================================================================================//

    & > #publications {
        width: 100%;
        height: max-content;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: ${colors.contrast};
        color: ${colors.black};
        font-family: ${fonts.normal};

        & > h1 {
            font-family: ${fonts.outlined};
            font-size: 48px;
            margin-top: 100px;
        }

        & > div {
            width: 100%;
            height: 100vh;
            display: flex;
            justify-items: center;
            align-items: center;

            & > div{
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                & > h1 {
                    font-family: ${fonts.outlined};
                    font-size: 48px;
                }

                & > div {
                    width: 40%;
                    justify-content: center;
                    align-items: center;
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                    margin-left: 40px;
                }

                & > a {
                    text-decoration: none;
                    font-size: 20px;
                    cursor: pointer;
                    margin-top: 50px;
                    color: ${colors.red};
                    :hover {
                        filter: opacity(75%);
                    }
                }
            }
        }
    }
`

export const StyledLink = {
    width: "100%",
    height: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: " center",
}

export const Icons = {
    marginTop: "30px",
    height: "50px",
    width: "50px",
    color: `${colors.contrast}`
}