import React from  "react"
import * as Scroll from "react-scroll"

import { Container, Header, Body, StyledLink, Icons } from "../styles/StyledHome"
import { FaLinkedin, FaGithubSquare, FaReact , FaNodeJs } from "react-icons/fa"
import { SiTypescript, SiJavascript, SiMongodb, SiJava} from "react-icons/si"

import Resume from "../assets/resume.pdf"
import Pokedex from "../assets/laptopPokedex.png"
import Weather from "../assets/laptopWeather.png"

const Home = () => {
    
    const Link = Scroll.Link

    return(
        <Container>
            <Header>
                <div>
                    <Link to="about" smooth={true} duration={250} style={StyledLink}>About</Link>
                </div>

                <div>
                    <Link to="education" smooth={true} duration={250} style={StyledLink}>Education</Link>
                </div>

                <div>
                    <Link to="skills" smooth={true} duration={250} style={StyledLink}>Skills</Link>
                </div>

                <div>
                    <Link to="publications" smooth={true} duration={250} style={StyledLink}>Publications</Link>
                </div>

                <div>
                    <Link to="contact" smooth={true} duration={250} style={StyledLink}>Contact</Link>
                </div>
            </Header>
            <Body>
                <div id="about">
                    <h3>Fullstack Developer</h3>
                    <h1>Gustavo Charamba</h1>
                    <a href={Resume} download>Download Resume</a>
                    <div>
                        <a href="https://www.linkedin.com/in/gustavocharamba/"><FaLinkedin style={Icons}/></a>
                        <a href="https://github.com/advaner"><FaGithubSquare style={Icons}/></a>
                    </div>
                </div>


                <div id="education">
                    <h1>Education</h1>
                    <ul>
                        <h3>High School</h3>
                        <li>
                            Colégio Evolução
                        </li>
                        <h3>Graduation</h3>
                        <li>
                            Electronic Engineering - (UFPE) Universidade Federal de Pernambuco (Graduating)
                        </li>
                    </ul>

                    <h1>Languages</h1>
                    <ul>
                        <li>
                            Portuguese - Native
                        </li>
                        <li>
                            English - Expert
                        </li>
                    </ul>
                </div>

                <div id="skills">
                    <div>
                        <h1>Skills</h1>
                        <div>
                            <div>
                                <SiJavascript style={{width: "75px", height: "100px", color: "#f0db4f"}}/>
                                <h4>Javascript</h4>
                            </div>
                            <div>
                                <SiMongodb style={{width: "75px", height: "100px", color: "#3FA037"}}/>
                                <h4>MongoDB</h4>
                            </div>
                            <div>
                                <SiTypescript style={{width: "75px", height: "100px", color: "#007acc"}}/>
                                <h4>Typescript</h4>
                            </div>
                            <div>
                                <FaReact style={{width: "75px", height: "100px", color: "#61DBFB"}}/>
                                <h4>React JS</h4>
                            </div>
                            <div>
                                <FaReact style={{width: "75px", height: "100px", color: "#61DBFB"}}/>
                                <h4>React Native</h4>
                            </div>
                            <div>
                                <FaNodeJs style={{width: "75px", height: "100px", color: "#3C873A"}}/>
                                <h4>NodeJS</h4>
                            </div>
                        </div>
                    </div>
                </div>

                <div id="publications">
                    <h1>Publications</h1>
                    <div id="pokedex">
                        <div>
                            <img src={Pokedex}/>
                        </div>
                        <div>
                            <h1>Pokedex</h1>
                            <div>
                                <SiJavascript style={{width: "80px", height: "100px", color: "#f0db4f"}}/>
                                <FaReact style={{width: "80px", height: "100px", color: "#61DBFB"}}/>
                                <SiTypescript style={{width: "80px", height: "100px", color: "#007acc"}}/>
                            </div>
                            <a href="https://github.com/advaner/pokedex">See on Github</a>
                        </div>
                    </div>

                    <div id="weather">
                        <div>
                            <img src={Weather}></img>
                        </div>
                        <div>
                            <h1>Weather Forecast</h1>
                            <div>
                                <SiJavascript style={{width: "80px", height: "100px", color: "#f0db4f"}}/>
                                <FaReact style={{width: "80px", height: "100px", color: "#61DBFB"}}/>
                                <SiTypescript style={{width: "80px", height: "100px", color: "#007acc"}}/>
                            </div>
                            <a href="https://github.com/advaner/weather-web">See on Github</a>
                        </div>
                    </div>
                </div>
            </Body>
        </Container>
    )
}

export default Home